'use client';

import { useState } from 'react';
import { Card, CardContent, CardHeader, CardTitle, CardDescription } from '@/components/ui/Card';
import { Button } from '@/components/ui/Button';
import { RatingScale } from '@/components/RatingScale';
import { Textarea } from '@/components/ui/Textarea';
import { Badge } from '@/components/ui/Badge';
import { Checkbox } from '@/components/ui/Checkbox';
import { mockUsers } from '@/data/mock';

const peers = [
  mockUsers['user-teacher-2'],
];

export default function PeerEvaluation() {
  const [selectedPeer, setSelectedPeer] = useState<string | null>(null);
  const [ratings, setRatings] = useState<Record<string, number>>({});
  const [comment, setComment] = useState('');
  const [submitted, setSubmitted] = useState(false);

  const handleSubmit = () => {
    setSubmitted(true);
    setTimeout(() => {
      setSelectedPeer(null);
      setRatings({});
      setComment('');
      setSubmitted(false);
    }, 2000);
  };

  return (
    <div className="space-y-6">
      <div>
        <h1 className="text-3xl font-bold text-gray-900 dark:text-white">Peer Evaluation</h1>
        <p className="text-gray-600 dark:text-gray-400 mt-2">
          Evaluate your colleague's teaching effectiveness
        </p>
      </div>

      {!selectedPeer ? (
        <Card>
          <CardHeader>
            <CardTitle>Select Colleague to Evaluate</CardTitle>
            <CardDescription>Choose a peer instructor to provide feedback</CardDescription>
          </CardHeader>
          <CardContent className="space-y-3">
            {peers.map((peer) => (
              <button
                key={peer.id}
                onClick={() => setSelectedPeer(peer.id)}
                className="w-full p-4 border-2 border-gray-200 dark:border-gray-700 rounded-lg text-left hover:border-purple-400 transition-colors"
              >
                <p className="font-semibold text-gray-900 dark:text-white">{peer.name}</p>
                <p className="text-sm text-gray-600 dark:text-gray-400">{peer.department}</p>
              </button>
            ))}
          </CardContent>
        </Card>
      ) : (
        <>
          {submitted ? (
            <Card>
              <CardContent className="pt-6 text-center py-12">
                <div className="inline-flex items-center justify-center w-16 h-16 bg-green-100 dark:bg-green-900/30 rounded-full mb-4">
                  <svg className="w-8 h-8 text-green-600 dark:text-green-400" fill="currentColor" viewBox="0 0 20 20">
                    <path fillRule="evenodd" d="M10 18a8 8 0 100-16 8 8 0 000 16zm3.707-9.293a1 1 0 00-1.414-1.414L9 10.586 7.707 9.293a1 1 0 00-1.414 1.414l2 2a1 1 0 001.414 0l4-4z" clipRule="evenodd" />
                  </svg>
                </div>
                <h2 className="text-2xl font-bold text-gray-900 dark:text-white mb-2">Evaluation Submitted</h2>
                <p className="text-gray-600 dark:text-gray-400 mb-6">
                  Thank you for your feedback.
                </p>
                <Button variant="primary" onClick={() => setSelectedPeer(null)}>
                  Evaluate Another Colleague
                </Button>
              </CardContent>
            </Card>
          ) : (
            <Card>
              <CardHeader>
                <CardTitle>
                  Evaluate {mockUsers[selectedPeer]?.name}
                </CardTitle>
                <CardDescription>Provide constructive feedback</CardDescription>
              </CardHeader>
              <CardContent className="space-y-6">
                <RatingScale
                  label="Teaching Quality"
                  value={ratings['teaching'] || 0}
                  onChange={(v) => setRatings({ ...ratings, teaching: v })}
                />

                <RatingScale
                  label="Collaboration & Teamwork"
                  value={ratings['collaboration'] || 0}
                  onChange={(v) => setRatings({ ...ratings, collaboration: v })}
                />

                <RatingScale
                  label="Professional Development"
                  value={ratings['development'] || 0}
                  onChange={(v) => setRatings({ ...ratings, development: v })}
                />

                <Textarea
                  label="Comments"
                  placeholder="Provide specific feedback..."
                  value={comment}
                  onChange={(e) => setComment(e.target.value)}
                  rows={4}
                />

                <Checkbox label="I confirm this evaluation is honest and fair" />

                <div className="flex gap-3">
                  <Button variant="outline" onClick={() => setSelectedPeer(null)}>
                    Back
                  </Button>
                  <Button
                    variant="primary"
                    onClick={handleSubmit}
                    disabled={Object.keys(ratings).length < 3}
                  >
                    Submit Evaluation
                  </Button>
                </div>
              </CardContent>
            </Card>
          )}
        </>
      )}
    </div>
  );
}
